<template>
  <div>
      <div>
    <!-- 头部标签 -->
    <van-nav-bar fixed>
      <template v-slot:left>
        <!-- <img class="logo" src="../../assets/toutiao_logo.png" alt="" /> -->
        <img class="logo" :src="imgObj"/>
        <!-- <van-icon name="search" size="0.48rem" color="#fff" @click="searchPage"/> -->
      </template>
      <template #right>
        <van-icon name="search" size="0.48rem" color="#fff"  @click="searchPage"/>
      </template>
    </van-nav-bar>
  </div>
  <div class="main">
      <van-tabs v-model="channelId" sticky offset-top="1.226667rem" animated @change="channelChangeFn" swipeable>
      <van-tab :title="obj.name" v-for="obj in userChannelList" :key="obj.id" :name="obj.id" >
        <ArticleList :channelId="channelId"></ArticleList></van-tab>
   </van-tabs>
   <!-- Tab 标签页 -->
<van-icon name="plus" size="0.37333334rem" class="moreChannels" @click="plusClickFn"/>
  </div>
      <van-popup v-model="show" get-container="body" class="channel_popup"

       >
        <ChannelEdit
        :userList="userChannelList"
        :unCheckList="unCheckChannelList"
         @addChannelEV="addChannelFn"
          @closeEV="close"
           @removeChannel="removeChannel"
           ref="editRef"
           v-model="channelId"
           ></ChannelEdit>
        </van-popup>
  </div>
</template>
<script>
import { getUserChannelsAPI, getAllChannelsAPI, updateChannelsAPI, removeChannelsAPI } from '@/api'
import ArticleList from './components/ArticleList.vue'
import logoPng from '../../assets/toutiao_logo.png'
import ChannelEdit from './ChannelEdit.vue'
// import { log } from 'console'
export default {
  name: 'Home',
  data () {
    return {
      imgObj: logoPng,
      channelId: 0,
      userChannelList: [],
      articleList: [],
      show: false,
      allChannelList: [],
      channelScrollTObj: {}
    }
  },
  async created () {
    const res = await getUserChannelsAPI()
    console.log(res)
    this.userChannelList = res.data.data.channels
    // this.channelChangeFn()
    const res2 = await getAllChannelsAPI()
    this.allChannelList = res2.data.data.channels
  },
  methods: {
    // async channelChangeFn () {
    //   const res2 = await getAllArticleListAPI({
    //     channel_id: this.channelId,
    //     timestamp: (new Date()).getTime()
    //   })
    //   console.log(res2)
    //   this.articleList = res2.data.data.results
    // }
    channelChangeFn () {
      this.$nextTick(() => {
        document.documentElement.scrollTop = this.channelScrollTObj[this.channelId]
        document.body.scrollTop = this.channelScrollTObj[this.channelId]
      })
    },
    plusClickFn () {
      this.show = true
    },
    async addChannelFn (channelObj) {
      this.userChannelList.push(channelObj)
      const newArr = this.userChannelList.filter(obj => obj.id !== 0)
      const theNewArr = newArr.map((obj, index) => {
        const newObj = { ...obj }
        delete newObj.name
        newObj.seq = index
        return newObj
      })
      // newArr.forEach((obj, index) => {
      //   delete obj.name
      //   obj.seq = index
      // })
      const res = await updateChannelsAPI({
        channels: theNewArr
      })
      console.log(res)
    },
    close () {
      this.show = false
      this.$refs.editRef.isEdit = false
    },
    async removeChannel (channelObj) {
      const index = this.userChannelList.findIndex(obj => obj.id === channelObj.id)
      this.userChannelList.splice(index, 1)
      const res = await removeChannelsAPI({
        channelId: channelObj.id
      })
      console.log(res)
    },
    searchPage () {
      this.$router.push('/search')
    },
    scrollFn () {
      this.$route.meta.scrollT = document.documentElement.scrollTop || document.documentElement.scrollTop
      this.channelScrollTObj[this.channelId] = document.documentElement.scrollTop || document.documentElement.scrollTop
    }
  },
  components: {
    ArticleList,
    ChannelEdit
  },
  computed: {
    unCheckChannelList () {
      const newArr = this.allChannelList.filter(bigObj => {
        const index = this.userChannelList.findIndex(smallObj => {
          return smallObj.id === bigObj.id
        })
        if (index > -1) {
          return false
        } else {
          return true
        }
      })
      return newArr
    }
  },
  activated () {
    console.log(this.$route)
    window.addEventListener('scroll', this.scrollFn)
    document.documentElement.scrollTop = this.$route.meta.scrollT
    document.body.scrollTop = this.$route.meta.scrollT
  },
  deactivated () {
    window.removeEventListener('scroll', this.scrollFn)
  }
}
</script>

<style scoped lang="less">

.logo {
    width: 100px;
    height: 30px;
}
.main {
  padding-top: 44px;
}
// 设置 tabs 容器的样式
/deep/ .van-tabs__wrap {
  padding-right: 30px;
  background-color: #fff;
}

// 设置小图标的样式
.moreChannels {
  position: fixed;
  top: 62px;
  right: 8px;
  z-index: 999;
}
.channel_popup{
  width: 100vw;
  height: 100vh;
}
</style>
