<template>
  <div>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
    <van-list v-model="loading"
    :finished="finished"
    finished-text="没有更多了"
    offset="50"
    @load="onLoad">
      <ArticleItem
      v-for="obj in list"
      :key="obj.art_id"
      :artObj="obj"
      @dislikeEV="dislikeFn"
      @reportEV="reportFn"
      offset="50"
      :immediate-check="false"
      @click.native="itemClickFn(obj.art_id)"
      ></ArticleItem>
      <!-- @click.native="$router.push(`/article_detail?aid=${item.art_id}`)" -->
      </van-list>
      </van-pull-refresh>
  </div>
</template>

<script>
import ArticleItem from '../../../components/ArticleItem.vue'
import { getAllArticleListAPI, dislikeArticleAPI, reportArticleAPI } from '@/api'
import { Notify } from 'vant'

export default {
  props: {
    channelId: Number
  },
  data () {
    return {
      list: [],
      loading: false, // 底部加载状态
      finished: false,
      theTime: new Date().getTime(),
      isLoading: false
      // 数据是否全部加载完成
    }
  },

  components: {
    ArticleItem
  },
  created () {
    this.getArticleListFn()
  },

  // async created () {
  //   const res = await getAllArticleListAPI({
  //     channel_id: this.channelId,
  //     timestamp: this.theTime
  //   })
  //   console.log(res)
  //   this.list = res.data.data.results
  //   this.theTime = res.data.data.pre_timestamp
  // },
  methods: {
    async getArticleListFn () {
      const res = await getAllArticleListAPI({
        channel_id: this.channelId,
        timestamp: this.theTime
      })
      console.log(res)

      this.list = [...this.list, ...res.data.data.results]
      this.theTime = res.data.data.pre_timestamp
      this.loading = false
      if (res.data.data.results.timestamp === null) {
        this.finished = true
      }
      this.isLoading = false
    },
    async onLoad () {
      if (this.list.length === 0) {
        this.loading = false
        return
      }
      // const res = await getAllArticleListAPI({
      //   channel_id: this.channelId,
      //   timestamp: this.theTime
      // })
      // console.log(res)

      // this.list = [...this.list, ...res.data.data.results]
      // this.theTime = res.data.data.pre_timestamp
      this.getArticleListFn()

      // if (res.data.data.results.timestamp === null) {
      //   this.finished = true
      // }
    },
    async onRefresh () {
      this.list = []
      this.theTime = new Date().getTime()
      // this.isLoading = false
      this.getArticleListFn()
    },
    async dislikeFn (id) {
      await dislikeArticleAPI({
        artId: id
      })
      Notify({ type: 'success', message: '反馈成功' })
    },
    async reportFn (id, value) {
      await reportArticleAPI({
        artId: id,
        type: value
      })
      Notify({ type: 'success', message: '举报成功' })
    },
    itemClickFn (id) {
      this.$router.push({
        path: `/detail?art_id=${id}`
      })
    }
  }
}
</script>

